import React from "react"
import Header from "../components/Header"
import styled from "styled-components"
import AniLink from "gatsby-plugin-transition-link/AniLink"

import "../components/layout.css"

const IndexSection = styled.section`
  text-align: center;
  margin: 100px 0;
`
const IndexTitle = styled.h1`
  font-size: 110px;
  margin-bottom: 10px;
`

const IndexSubtitle = styled.p`
  font-size: 22px;
  letter-spacing: 1px;
`

const IndexButton = styled.button`
  font-family: "Nunito", sans-serif;
  width: 300px;
  cursor: pointer;
  color: #fff;
  border: none;
  outline: none;
  height: 65px;
  margin-top: 22px;
  background-color: #252d4f;
  font-size: 16px;
  -webkit-box-shadow: -2px 0px 20px 4px rgb(0 0 0 / 25%);
  box-shadow: -2px 0px 20px 4px rgb(0 0 0 / 25%);
`

export default function Home() {
  return (
    <div className="wrapper">
      <Header />
      <IndexSection>
        <IndexTitle>Michael Diggs</IndexTitle>
        <IndexSubtitle>
          Front End Developer with a passion for learning more about the web.
        </IndexSubtitle>
        <AniLink paintDrip duration={0.4} hex="#212840" to="/projects">
          <IndexButton>View My Projects</IndexButton>
        </AniLink>
      </IndexSection>
    </div>
  )
}
